import React from 'react';
import { Link } from 'react-router-dom';

class SingleRecipe extends React.Component {
	constructor(props) {
		super(props)
		const id = this.props.match.params.id;
		this.state = {
			recipe: {},
			id:id,
			loading: true
		}
	}

	async componentDidMount(){
		const url =`https://www.food2fork.com/api/get?key=0a55b2d26754e3da01b0145eb70b9e31&rId=${this.state.id}`;

		try {
			const response = await fetch(url);
			const responseData = await response.json();
			this.setState({
				recipe:responseData.recipe,
				loading: false
			})
		} catch (error) {
			console.log(error)
		}
	}
	render() {
		const { image_url, title, sourece_url,publisher, publisher_url, ingredients } = this.state.recipe;
		if (this.state.loading === true) {
			return (
				<div className="container">
					<div className="row">
						<div className="col-10 mx-auto col-md-6 my-3">
							<h2 className="text-uppercase text-orange text-center">
								Loading Recipe...
              </h2>
						</div>
					</div>
				</div>
			)
		}
		return (
			<div className="container mb-5">
				<div className="row">
					<div className="col-10 mx-auto col-md-6 my-3">
						<Link to='/recipes' className='btn btn-warning mb-5 text-capitalize'>Back to Recipes</Link>
						<img 
							src={ image_url } 
							className="d-block w-100"
							style={{maxHeight: "30rem" }}/>
					</div>
					<div className="col-10 mx-auto col-md-6 my-3">
						<h6 className="text-uppercase">{title}</h6>
						<h6 className="text-warning text-capitalize text-slanted">Provided by { publisher }</h6>
						<a 
							href={ publisher_url } 
							className="btn btn-primary mt-2 text-capitalize"
							target="_blank" 
							rel="noopener noreferrer">publisher Webpage</a>
						<a 
							href={ sourece_url } 
							className="btn btn-success mt-2 text-capitalize mx-2"
							target="_blank" 
							rel="noopener noreferrer">Recipe Url</a>
						
						<ul className="list-group mt-4">
							<h2 className="mt-3 mb-4"> Ingredients</h2>
							{ ingredients.map( (ingredient, index) =>{
								return(
									<li 
										className="list-group-item text-slanted"
										key={ index }>
										{ ingredient }
									</li>
								)
							})}
						</ul>
						
					</div>
				</div>
			</div>
		)

	}
}

export default SingleRecipe;