import React from 'react';

import RecipeList from '../components/RecipeList';
import Search from '../components/Search';

class Recipes extends React.Component {
    constructor(props) {
        super(props)
    }

    state = {
        recipes: [],
        search: '',
        url: `https://www.food2fork.com/api/search?key=0a55b2d26754e3da01b0145eb70b9e31`,
        baseUrl: `https://www.food2fork.com/api/search?key=0a55b2d26754e3da01b0145eb70b9e31`,
        query: "&q=",
        error: ''
    }
    componentDidMount() {
        this.getRecipes()
    }

    getRecipes = async () => {
        try {
            const data = await fetch(this.state.url);
            const jsonData = await data.json();
            if (jsonData.recipes.length === 0) {
                this.setState({
                    error: 'Sorry, no recipe found'
                })
            } else {
                this.setState({
                    recipes: jsonData.recipes,
                    error: ''
                })
            }
        } catch (error) {
            console.log(error)
        }
    }

    handleChange = (e) => {
        this.setState({
            search: e.target.value
        })
    }

    handleSubmit = (e) => {
        e.preventDefault();
        const { baseUrl, query, search } = this.state;
        this.setState({
            url: `${baseUrl}${query}${search}`,
            search: ''
        }, () => this.getRecipes())

    }


    render() {
        return (
            <>
                <Search
                    search={this.state.search}
                    handleChange={this.handleChange}
                    handleSubmit={this.handleSubmit} />
                {this.state.error ? (<section><div className="row"><div className="col"><h2 className="text-orange text-center text-uppercase mt-5">{this.state.error}</h2></div></div></section>) : <RecipeList recipes={this.state.recipes} />}
            </>
        );
    }
}

export default Recipes;